.portfolio{
    margin: 96px auto;
    width: 90%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
}

.photos img{
    width: 100%;
    border-radius: 10px;
    display: block;
}

.photos{
    flex-basis: 31%;
    position: relative;
}

.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 15, 152, 0.9);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: #fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;
}

.caption img{
    width: 60px;
    margin-bottom: 10px;
}

.photos:hover .caption{
    opacity: 1;
    padding-top: 0;
}

.sizing{
    width: 1366px;
    height: 310px;
}